/*DrumPad.css*/
.drum-pad-container {
	grid-column: 1 / 1;
	grid-row: 2 / 3;
    display: grid;
    grid-template-columns: repeat(3, 80px);
    grid-template-rows: repeat(3, 80px);
    grid-gap: 8px;
	margin: auto;
}
.drum-pad {
	display: inline-block;
	border-radius: 4px;
	cursor: pointer;
	border: 1px solid #1D1F1E;
}
.drum-pad.blue {
	background: rgb(1,153,229);
	background: -webkit-radial-gradient(circle, rgba(1,153,229,1) 0%, rgba(44,71,149,1) 100%);
	background: radial-gradient(circle, rgba(1,153,229,1) 0%, rgba(44,71,149,1) 100%);
}
.drum-pad.purple {
	background: rgb(236,33,243);
	background: -webkit-radial-gradient(circle, rgba(236,33,243,1) 0%, rgba(177,25,182,1) 100%);
	background: radial-gradient(circle, rgba(236,33,243,1) 0%, rgba(177,25,182,1) 100%);
}
.drum-pad.yellow {
	background: rgb(223,226,11);
	background: -webkit-radial-gradient(circle, rgba(223,226,11,1) 0%, rgba(167,170,8,1) 100%);
	background: radial-gradient(circle, rgba(223,226,11,1) 0%, rgba(167,170,8,1) 100%);
}
.drum-pad.green {
	background: rgb(0,214,120);
	background: -webkit-radial-gradient(circle, rgba(0,214,120,1) 0%, rgba(0,107,60,1) 100%);
	background: radial-gradient(circle, rgba(0,214,120,1) 0%, rgba(0,107,60,1) 100%);
}
/*Metronome.css*/
.metronome {
    display: flex;
    flex-direction: column;
    text-align: center;
	font-family: 'Sanchez', serif;
	grid-row: 2 / 3;
}
.metronome-btn {
    width: 64px;
    display: flex;
    margin: 0 auto;
}
.metronome-btn > img {
	max-width: 100%;
}
.metronome-btn.down {
	-webkit-transform: rotate(180deg);
	        transform: rotate(180deg);
}
/*PianoPad.css*/
.piano-pad-container {
	grid-column: 1 / 3;
	grid-row: 3 / 3;
	display: grid;
	grid-template-columns: auto 1px auto 1px auto 1px auto auto 1px auto 1px auto 1px;
	width: inherit;
}
.piano-pad-key {
	display: block;
	border: 1px solid rgba(218, 232, 242, 0.6);
	outline: 1px solid #1D1F1E;
	border-radius: 2px;
	position: relative;
}
.piano-pad-key.white {
	width: auto;
    height: 100%;
    background-color: #FEFEFE;
    z-index: 1;
}
.piano-pad-key.black {
	width: 36px;
	height: 64%;
	margin-left: -18px;
	background-color: #0C091D;
	z-index: 2;
}
.piano-pad-key.pressed, .drum-pad.pressed {
	-webkit-animation: animation 1000ms linear both;
	animation: animation 1000ms linear both;
}
.piano-pad-key::after {
	content: "";
	display: block;
	height: 0px;
	width: 0px;
	border: none;
	border-radius: 100%;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	z-index: 1000;
	-webkit-transition: all ease 0.5s;
	transition: all ease 0.5s;
}
.piano-pad-key.pressed::after {
	border: 1px solid yellow;
	display: block;
	height: 40px;
	width: 40px;
}
