/*Instructions.css*/
.ui-instructions {
    z-index: 1;
    position: absolute;
    height: 256px;
    display: flex;
    cursor: pointer;
	left: calc(50% - 256px);
    -webkit-transform: rotate(-2deg);
            transform: rotate(-2deg);
	-webkit-transition: all cubic-bezier(0.26, -0.23, 0.14, 1.35) 0.4s;
	transition: all cubic-bezier(0.26, -0.23, 0.14, 1.35) 0.4s;
}
.ui-instructions.drum {
	top: calc(50% - 224px);
}
.ui-instructions.piano {
	top: calc(50% + 52px);
}
.ui-instructions:hover {
	
}
.ui-instructions.active {
	z-index: 2;
}

@media screen and (max-height: 700px) {
	.ui-instructions.drum {
	    top: calc(50% - 200px);
		left: calc(50% - 441px);
	}
	.ui-instructions.piano {
	    top: calc(50% - 80px);
		left: calc(50% - 441px);
	}
}
.ui-instructions.active {
  -webkit-animation: uiAnimation 1000ms linear both;
  animation: uiAnimation 1000ms linear both;
}

